import { Modal } from "@chac/common/components/Modal";
import styled from "styled-components";

import React from "react";
import { useAlert } from "../hooks/useModal";
/**
 * Dimmed 등 기본적인 modal css를 위해 <Modal type="custom" > 로 감싸준다
 */

const StyledContainer = styled.div`
	.image-list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; 
		.flex-item {
			flex: 33%;
			margin: 15px 0;
			.sample-image {
				width: 96px;
				height: 96px;
				border-radius: 5px;
				background-color: plum;
			}
			.hidden {
				visibility: hidden;
			}
		}
	}
`;

export default function SelectProfileBottomSheet({data, close}) {
	// 샘플이미지
	const imageList = [
		'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/1.png',
		'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/4.png',
		'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/7.png',
		'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/10.png',
	]
	const [showAlert] = useAlert()
	// 프로필 신규 추가 이벤트 (카메라 open)
	const addNewProfileClick = (e) => {
		showAlert({
			title: '신규 이미지 등록',
			content: '카메라 연동 예정, 일단 alert만 띄워두기'
		})
	}
	const imageClick = (src) => {
		close({image: src});
	}
	return (
		<Modal type="bottomSheet" close={close}>
			<StyledContainer>
				<h2>프로필 이미지</h2>
				<div className="image-list">
					{imageList.map((item, idx) => (
						<div className="flex-item" key={idx} onClick={(e) => imageClick(item)}> <img className="sample-image" src={item} /> </div>
					))}
					<div className="flex-item">
						<img className="sample-image" onClick={addNewProfileClick}/>				
					</div>
					<div className="flex-item">
						<img className="hidden"></img>				
					</div>
				</div>

			</StyledContainer>
		</Modal>
	);
}
